export default {		
	data() {
		return {
			planid:'',
			list:'',
			rescount:""
		}
	},
	onLoad(e) {
		this.planid = e.id
		this.getData()
	},
	methods: {
		getData(){
			this.$store.dispatch('quickLisDetailt',{
				planid:this.planid
			}).then(res=>{
				this.list = res.data
				this.rescount = res.rescount
			}).catch(err=>{})
		},
		stop(){
			uni.showModal({
		    title: '提示',
		    content: '终止执行？',
		    success: (res)=> {
		        if (res.confirm) {
		            this.$store.dispatch('stopPlan',{
		            	planid:this.planid
		            }).then(res1=>{
		            	uni.showToast({
		            		title: res1.msg,
		            		icon: 'none', //loading   none  success
		            		success: (res2)=>{
		            			this.getData()
		            		}
		            	});
		            }).catch(err=>{})
		        } else if (res.cancel) {
		            
		        }
		    }
			});
		}
	},
	onNavigationBarButtonTap(){
		this.stop()
	}
}
